body {
  background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
    url("./hero-2.jpg");
  background-size: cover;
  background-position: center;
  margin: 0;
  font-family: Arial, sans-serif;
  position: relative;
}

.main-content {
  position: relative;
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px) brightness(0.9);
  border-radius: 15px;
  padding: 20px;
  max-width: 1200px;
  margin: 20px auto;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.elements-grid {
  display: grid;
  grid-template-columns: repeat(14, 1fr);
  gap: 10px;
  padding: 20px;
  max-width: 100%;
  margin: 0 auto;
}

.element {
  padding: 10px;
  border: 1px solid rgb(255, 255, 255);
  text-align: center;
  cursor: pointer;
  transition: transform 0.3s, box-shadow 0.3s;
  border-radius: 8px;
  color: white;
  backdrop-filter: blur(5px);
  position: relative;
  min-width: 60px;
}

.element .atomic-number {
  position: absolute;
  top: 5px;
  left: 5px;
  font-size: 10px;
  font-weight: bold;
  color: rgba(255, 255, 255, 0.7);
}

.element .symbol {
  font-size: 18px;
  font-weight: bold;
  margin-top: 10px;
}

.element .state {
  font-size: 10px;
  color: rgba(255, 255, 255, 0.8);
  margin-top: 5px;
}

.element:hover {
  transform: scale(1.05);
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.15);
  background-color: rgba(255, 255, 255, 0.2);
}

.element.solid {
  color: rgba(255, 255, 255, 0.998);
  font-weight: bold;
  -webkit-text-stroke: 0.2px rgb(65, 65, 65);
}

.element.liquid {
  color: rgb(217, 255, 63);
  font-weight: bold;

  -webkit-text-stroke: 0.2px rgb(65, 65, 65);
}

.element.gas {
  color: rgb(33, 240, 255);
  font-weight: bold;

  -webkit-text-stroke: 0.2px rgb(65, 65, 65);
}

@media (max-width: 1024px) {
  .elements-grid {
    grid-template-columns: repeat(7, 1fr);
  }
}

@media (max-width: 768px) {
  .elements-grid {
    grid-template-columns: repeat(6, 1fr);
  }

  .element {
    padding: 5px;
    font-size: 14px;
  }
}

@media (max-width: 480px) {
  .elements-grid {
    grid-template-columns: repeat(4, 1fr);
  }

  .element {
    padding: 5px;
    font-size: 12px;
  }
}
